@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,800);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #242424; }

.search-box {
  width: 80%;
  display: flex; }
  @media screen and (max-width: 767px) {
    .search-box {
      width: 100%; } }
  .search-box .input-box {
    width: 100%;
    height: 3rem;
    background: transparent;
    border: none;
    border-bottom: 1px solid #929292;
    box-sizing: border-box;
    font-size: 2rem;
    font-weight: 300;
    color: #d3d3d3; }
    @media screen and (max-width: 767px) {
      .search-box .input-box {
        font-size: 1.1rem;
        height: 2rem; } }
    .search-box .input-box:focus {
      outline: none; }
  .search-box .search-button {
    background: transparent;
    border: 1px solid #929292;
    cursor: pointer; }
    .search-box .search-button .fa-search {
      color: #929292;
      font-size: 2rem; }
      @media screen and (max-width: 767px) {
        .search-box .search-button .fa-search {
          font-size: 1.1rem; } }
    .search-box .search-button:hover {
      border: 1px solid #d3d3d3; }
      .search-box .search-button:hover .fa-search {
        color: #d3d3d3; }
    .search-box .search-button:active {
      border: 1px solid #ffffff; }
      .search-box .search-button:active .fa-search {
        color: #ffffff; }
    .search-box .search-button:focus {
      outline: none; }

.card {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 280px;
  max-width: 310px;
  width: 100%;
  color: #333;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  font-size: 16px; }
  .card * {
    box-sizing: border-box;
    -webkit-transition: all 0.35s ease;
    transition: all 0.35s ease; }
  .card .image {
    position: relative;
    border-bottom: 4px solid #34495e;
    z-index: 1;
    height: 20rem;
    margin: 0; }
    .card .image:before {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 20px 20px 0 20px;
      border-color: #34495e transparent transparent transparent;
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      -webkit-transform: translate(-50%, -15px);
              transform: translate(-50%, -15px);
      z-index: -1;
      -webkit-transition: all 0.35s ease;
      transition: all 0.35s ease; }
    .card .image img {
      max-width: 100%;
      max-height: 100%;
      height: 100%;
      vertical-align: top;
      background-color: #929292; }
  .card .caption {
    height: 14rem;
    background-color: #ffffff;
    padding: 25px; }
    .card .caption .social-media i {
      margin-right: 5px;
      display: inline-block;
      font-size: 24px;
      color: #000000;
      width: 35px;
      height: 35px;
      line-height: 35px;
      background: white;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
      background-color: rgba(0, 0, 0, 0.05); }
      .card .caption .social-media i:hover {
        background-color: #242424;
        color: white !important; }

.representatives-section {
  display: flex;
  flex-flow: wrap;
  justify-content: center; }
  .representatives-section .loader {
    display: block;
    color: #d3d3d3; }

.app {
  width: 100%; }
  .app .page {
    max-width: 1370px;
    margin-left: auto;
    margin-right: auto;
    padding: 50px 20px; }
    .app .page .title {
      color: #d3d3d3;
      font-size: 5rem;
      margin-top: 10rem;
      margin-bottom: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      -webkit-transition: margin-top .5s ease, font-size .5s ease;
      transition: margin-top .5s ease, font-size .5s ease; }
      @media screen and (max-width: 767px) {
        .app .page .title {
          font-size: 2rem;
          margin-bottom: 1rem;
          margin-top: 6rem; } }
      @media screen and (min-width: 768px) and (max-width: 1199px) {
        .app .page .title {
          font-size: 3rem;
          margin-bottom: 2rem; } }
      .app .page .title.top {
        margin-top: 0; }
        @media desktopOnly {
          .app .page .title.top {
            font-size: 3rem; } }
    .app .page .address-search-wrapper {
      display: flex;
      justify-content: center;
      margin-bottom: 2rem; }

